.profile__info {
  flex-basis: 60%;
  display: flex;
  flex-direction: column;
  max-width: 60%;
  
}


@media all and (max-width: 930px) {
  .profile__info {
    flex-basis: 50%;
  }
}

@media all and (max-width: 740px) {
  .profile__info {
    flex-basis: 0;
    align-items: center;
    margin: 10px 0 36px 0;
    justify-content: flex-start;
    width: 100%;
    max-width: 100%;
  }
}